import React from 'react'

export const Contact = () => {
    return (
        <div className="main-content">
            <div className="main-content-inner">
        <div className="contact" id="contact">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-lg-12">
                        <div className="section-heading wow fadeInUp" data-wow-delay="0.2s">
                            <h2 className="title">
                                Get In <span className="color">Touch</span>
                                <span className="bg-text">Contact</span>
                            </h2>
                        </div>
                    </div>
                </div>
                <div className="row cAndm">
                    <div className="col-lg-12">
                        <div className="home-page-form">
                            <div className="contact-form">
                                <form id="contact-form" action="https://formspree.io/f/xoqyyakz" method="POST">
                                    <div className="controls">
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <input id="name" type="text" name="name" className="form-control" placeholder="Name*" required="required" data-error="Name is required." />
                                                    <div className="help-block with-errors"></div>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <input id="email" type="email" name="_replyto" className="form-control" placeholder="Email*" required="required" data-error="Valid email is required." />
                                                    <div className="help-block with-errors"></div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-md-12">
                                                <div className="form-group">
                                                    <input id="subject" type="text" name="subject" className="form-control" placeholder="Subject*" required="required" data-error="Subject is required." />
                                                    <div className="help-block with-errors"></div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-md-12">
                                                <div className="form-group">
                                                    <textarea id="message" name="message" className="form-control" placeholder="Message*" rows="7" required="required" data-error="Please,leave us a message."></textarea>
                                                    <div className="help-block with-errors"></div>
                                                </div>
                                            </div>
                                            <div className="col-md-12">
                                                <button type="submit" className="mybtn3 mybtn-bg"><span>Send Message</span></button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
        </div>
    )
}
